import React from 'react';
import s from '../mainPostItems/post.module.css';
import {Route, Link} from 'react-router-dom';
import AddComment from './AddComment';
import UserStats from '../mainPostItems/UserStats';
import Body from '../mainPostItems/Body';
//—Ç—É–ø–∞—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞.
const Comments = (props) => {

	const userData = props.comments.userData;
	const commentsData = props.comments.commentsData;

    return (
        <div className={s.mainItem}>
	        <div className={s.userItem}>
		        { 
		          userData.map(ud => {
		            return (
						<UserStats img={ud.img}
								count={ud.count}
								joined={ud.joined} 
								name={ud.name}
								id={ud.id} 
			            />
		            )
		          }) 
		        }
		    </div>

		    <div className={s.bodyItem}>
		        { 
		          commentsData.map(cd => {
		            return (
						<Body tag={cd.tag}
							text={cd.text} 
							date={cd.date}
							inReply={cd.inReply}
						/>            	
		            )
		          }) 
		        }
		    </div>   
		    <Route path='/post/discussion' render={() => {
		    	return (
		    		<AddComment addPost={props.addPost} 
		    					changeArea={props.changeArea}
		    					newCommentText={props.comments.newCommentText}
		    		/>
		    	)}} 
			/>
        </div>    
  	)
  	debugger
}

export default Comments;