import React from 'react';
import s from './Header.module.css';

const Header = () => {
    return (
		<div id="wrap-head">
			<div id="site-header">
				<div className="chunk">
					<div id="site-logo">
						<a className="site-logo" href="" title="Board index">Here should be site logo</a>
						<p className="skiplink"><a href="">Skip to content</a></p>
					</div>

					<ul id="site-menu">
						<li data-skip-responsive="true" className="site-menu"><a href="" rel="help" title="Frequently Asked Questions">FAQ</a></li>
						<li className="site-menu">
							<a href="" title="Example">Drop Down</a>
							<ul>
								<li><a href="">Lorem ipsum</a></li>
								<li><a href="">Welcome to phpBB3</a></li>
								<li><a href="">Frequently Asked Questions</a></li>
								<li><a href="">BBCode example</a></li>
							</ul>
						</li>
					</ul>
				</div>
			</div>

			<div id="site-nav" role="navigation">
				<div className={s.chunkNav}>
					<div className="site-nav">
						<div id="site-search" role="search">
							<form method="get">
								<fieldset>
									<input name="keywords" type="search" maxlength="128" title="Search for keywords" size="20" value="" placeholder="Search" />
									<button type="submit" title="Search">
										<i className="fa fa-search"></i>
									</button>
									<input type="hidden" name="style" value="3" />
								</fieldset>
							</form>
						</div>
						
						<ul role="menubar">
							<li className="font-icon rightside" data-skip-responsive="true">
								<a href="" title="Login" accesskey="x" role="menuitem">
									<i className="fa fa-power-off"></i>
									<span className="nav-rh-2">Login</span>
								</a>
							</li>
						</ul>
					</div>
				</div>
			</div>
		</div>
    )
}

export default Header;