import React from 'react';
import s from './post.module.css';
import {Route, Link} from 'react-router-dom';
//—Ç—É–ø–∞—è –∫–æ–º–ø–∞–Ω–µ–Ω—Ç–∞
const Post = (props) => {
    const path = "/post/" + props.postId;
    const tag = props.mainTag;
    const text = props.msg;
    const comments = props.commentCount;
    const username = props.username
    const date = props.date

    return (
      <li className="row">
        <dl className="icon forum_read">
          <dt title="No unread posts">
            <div className="list-inner">
              <Link className="forumtitle" to={path}>{tag}</Link>                    
              <div className="responsive-show">
                {text.length > 350 ? `${text.slice(0, 350)}...` : text}
              </div>
            </div>
          </dt>
            <dd className="posts">{comments}<dfn>Last comment</dfn></dd>
              <dd className="lastpost">
                <dfn>Last post</dfn>
                <Link className="lastsubject" to={path}>{tag}</Link>
                  <br />
                    by 
                <Link to="" style={{color: '#00AA00'}} className="username-coloured"> {username}</Link>
                <br />
                {date}          
              </dd>
        </dl>
      </li>
  )
}

export default Post;