import React from 'react';
import {connect} from 'react-redux';
import SidebarContent from './SidebarContent';

const mapStateToProps = state => {
	return {
		user: state.userdata.userEnteredData,
	}
}

const mapDispatchToProps = dispatch => {
	return {
		userName: (text) => {
			let action = {type: 'ADD-NAME', text: text}
			dispatch(action);
		},

		userPass: (text) => {
			let action = {type: 'ADD-PASS', text: text}
			dispatch(action);		
		},

		userStay: (bool) => {
			let action = {type: 'ADD-STAY', bool: bool}
			dispatch(action);
		},

		checkUser: () => {
			let action = {type: 'CHECK-USER'}
			dispatch(action);
		},
	}
}

const SidebarContext = connect(mapStateToProps, mapDispatchToProps)(SidebarContent)

export default SidebarContext