const initialState = {
	users: [
		{
			id: 1,
			name: 'Shura BI-2',
			email: 'ratan@gmail.com',
			pass: 'ifYouIn_hell',
			isStay: false,
		},
	],
	userEnteredData: {
		email: '',
		pass: '',
		isStay: false,
	}     
}

const userReducer = (state = initialState, action) => {

	let newState = {
		...state,
		users: [...state.users],
		userEnteredData: {...state.userEnteredData},
	};

	const _userName = text => {
		newState.userEnteredData.email = text
	}

	const _userPass = text => {
		newState.userEnteredData.pass = text
	}

	const _userStay = bool => {
		newState.userEnteredData.isStay = bool
	}

	const _checkUser = () => {
		newState.users.map(el => {
			if (
		  		newState.userEnteredData.email === el.email &&
		  		newState.userEnteredData.pass === el.pass
		  	)
		  		{
			  		el.isStay = newState.userEnteredData.isStay;

			  		newState.userEnteredData.email = '';
			  		newState.userEnteredData.pass = '';
			  		newState.userEnteredData.isStay = false;

			  		console.log(newState)
			  	}
		  	else {
		  		console.log('error');

			  	newState.userEnteredData.email = '';
			  	newState.userEnteredData.pass = '';
			  	newState.userEnteredData.isStay = false
		  	}			
		})
	  	
	}

    switch (action.type) {
      	case 'ADD-NAME':
			_userName(action.text)
			return newState;

		case 'ADD-PASS':
			_userPass(action.text)
			return newState;

		case 'ADD-STAY':
			_userStay(action.bool)
			return newState;

		case 'CHECK-USER':
			_checkUser()
			return newState;

		default:
			return state;
    }
}

export default userReducer;