import React from 'react';
import s from './posts.module.css';
import Post from './post/post';
import SidebarContext from './sidebar/SidebarContext';
import {Link} from 'react-router-dom';
//—É–º–Ω–∞—è –∫–æ–º–ø–∞–Ω–µ–Ω—Ç–∞. –£–º–µ–µ—Ç –æ—Ç—Ä–∏—Å–æ–≤—ã–≤–∞—Ç—å –ø–æ—Å—Ç—ã. 
const Posts = (props) => {

  const post = props.post

    return (
      <div>
        <SidebarContext />
        <div id="forumlist">
          <div id="forumlist-inner">
            <div className="forabg">
              <div className="inner">
                <ul className="topiclist">
                  <li className="header">
                    <dl className="icon">
                      <dt>
                        <div className="list-inner">
                          <Link to="">Main category</Link>
                        </div>
                      </dt>
                      <dd className="posts">Comments</dd>
                      <dd className="lastpost">Last post</dd>
                    </dl>
                  </li>
                </ul>
                <ul className="topiclist forums" style={{backgroundColor: '#FAFAFA'}}>
                  { 
                    post.map(p => {
                      return <Post mainTag={p.mainTag}
                            username={p.username}
                            commentCount={p.commentCount}
                            msg={p.msg}
                            postId={p.postId}
                            date={p.date}
                      />
                    }) 
                  }
                </ul>
             </div>
            </div>
          </div>
        </div>
      </div>
  )
}

export default Posts;