import React from 'react';
import '../style/style.css';
import Header from './mainComponents/Header/Header';
import Profile from './mainComponents/Profile/Profile';
import PostItem from './mainComponents/Post/PostItem'
import {Route} from 'react-router-dom';
// —É–º–Ω–∞—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞. –†—É–∫–æ–≤–æ–¥–∏—Ç –≤—Å–µ–º –ø—Ä–æ—Ü–µ—Å—Å–æ–º. –ù–æ –ø—Ä–∞–≤–∞ —É –Ω–µ–µ –æ–≥—Ä–∞–Ω–∏—á–µ–Ω—ã –∏ –æ–Ω–∞ —Ç–æ–ª—å–∫–æ –ø–µ—Ä–µ–¥–∞–µ—Ç —É–∫–∞–∑–∞–Ω–∏—è. –ö–æ–º–ø–æ–Ω–µ–Ω—Ç–∞-–≥–æ–Ω–µ—Ü
const Main = (props) => {
  return (
	    <div>
		    <div className='app-wrapper'>
				<Header />
				<Route exact path='/' render={() => <Profile />} />
				<Route path='/post' render={() => <PostItem />} />
		    </div>
		   </div>

    );
}

export default Main